
@import "../../scss/variables";
.jqvmap-label {
	pointer-events: none;
	position: absolute;
	display: none;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	background: #292929;
	color: #fff;
	font-family: sans-serif,Verdana;
	font-size: smaller;
	padding: 3px;
}
.jqvmap-pin {
	pointer-events: none;
}
.jqvmap-zoomin {
	position: absolute;
	left: 10px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	background: #000;
	padding: 3px;
	color: #fff;
	min-width: 10px;
	min-height: 10px;
	cursor: pointer;
	line-height: 10px;
	text-align: center;
	top: 10px;
}
.jqvmap-zoomout {
	position: absolute;
	left: 10px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	border-radius: 3px;
	background: #000;
	padding: 3px;
	color: #fff;
	min-width: 10px;
	min-height: 10px;
	cursor: pointer;
	line-height: 10px;
	text-align: center;
	top: 30px;
}
.jqvmap-region {
	cursor: pointer;
}
.jqvmap-ajax_response {
	width: 100%;
	height: 500px;
}
